.contact-form {
  width: 100%;
  margin-top: 2rem;
}

.contact-name,
.contact-email {
  width: 100%;
  height: 2.5rem;
  margin-bottom: 1rem;
}

.contact-name input,
.contact-email input {
  pointer-events: all;
  width: 100%;
  height: 100%;
  background-color: #262626;
  color: #fff;
  border: none;
  border-radius: 0.5rem;
}

.contact-message {
  width: 100%;
  height: 100%;
}

.contact-message textarea {
  width: 100%;
  height: 8rem;
  border-radius: 0.5rem;
  border: none;
  background-color: #262626;
  color: #fff;
}

.contact-form .join-today {
  margin-bottom: 5rem;
}
