.contact {
  display: flex;
  max-width: 100%;
  padding: 0.725rem;
  z-index: 1;
  position: relative;
  justify-content: center;
  flex-direction: column;
}

.contact .shadow {
  color: #262626;
  font-size: 4.9rem;
}

.contact-heading {
  font-size: 2.265rem;
  margin: 0;
  color: #000;
  z-index: 10;
  position: absolute;
  top: 0;
  margin-top: 2.625rem;
  margin-left: 0.25rem;
}

.contact .text-decoration {
  margin-top: 5.25rem;
}

.contact-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.contact-map {
  width: 100%;
  background-color: #262626;
  padding: 1rem;
  border-radius: 15px;
}

.contact-address {
  margin-top: 1rem;
  color: #fff;
}
