.footer {
  width: 100%;
  background-color: #262626;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0.725rem;
}

.footer-icons {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.social-icon {
  color: #fff;
  font-size: 2.5rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.footer-links {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-bottom: 2rem;
}

.footer-links-column {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  margin-left: 3rem;
}

.footer-links a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 1rem;
  font-size: 0.825rem;
  opacity: 70%;
}

.footer-copyright {
  color: #fff;
  width: 100%;
  text-align: center;
}

.footer-links a:hover,
.social-icon:hover {
  color: #f2b872;
}
